import React from 'react';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import { Link } from 'gatsby';
import { Typography, Tooltip, Grid, Card, CardContent, Divider } from '@material-ui/core';

const GridListTileComponent = ({ node }) => {
  return (
    <Grid item xs={12} sm={6} md={4} lg={3}>
      <Link to={node.fields.slug} style={{ textDecoration: 'none' }}>
        <Tooltip title={node.frontmatter.title} classes={{ tooltip: 'custom-tooltip' }}>
          <Card style={{ maxWidth: 345, boxShadow: 'none', position: 'relative' }}>
            <div style={{ position: 'absolute', top: '10px', right: '10px', backgroundColor: 'transparent', color: 'red', padding: '2px 5px', fontSize: '0.7em', fontWeight: 'bold', zIndex: 1 }}>
              {node.frontmatter.article}
            </div>
            <GatsbyImage
              image={getImage(node.frontmatter.image)}
              alt={node.frontmatter.title}
              style={{ height: 140 }}
            />
            <CardContent>
              <Typography variant="h5" component="h2">
                {node.frontmatter.title}
              </Typography>
              <Divider style={{ margin: '10px 0' }} />
              <Typography variant="body2" color="textSecondary" component="p">
                {node.frontmatter.summary}
              </Typography>
            </CardContent>
          </Card>
        </Tooltip>
      </Link>
    </Grid>
  );
};

export default GridListTileComponent;
