import React from 'react';
import { useStaticQuery, graphql, Link } from 'gatsby';
import { Grid, Button, Tooltip, Typography } from '@material-ui/core';
import groupBy from 'lodash/groupBy';
import GridListTileComponent from '../components/GridListTileComponent';

function toTitleCase(str) {
  return str.replace(/\w\S*/g, function (txt) {
    return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
  });
}

export default function HomeComponent() {
  const data = useStaticQuery(graphql`
  query {
    allMarkdownRemark(sort: { fields: [frontmatter___time], order: DESC }) {
      edges {
        node {
          fields {
            slug
            folderName
          }
          frontmatter {
            title
            time
            article
            summary
            image {
              childImageSharp {
                gatsbyImageData(
                  layout: CONSTRAINED
                  placeholder: BLURRED
                  aspectRatio: 2.33
                  width: 900
                )
              }
            }
          }
        }
      }
    }
  }
  
  `);

  const folders = groupBy(data.allMarkdownRemark.edges, 'node.fields.folderName');

  return (
    <>
      {Object.entries(folders).map(([folderName, nodes]) => (
        <>
          <div style={{ display: 'flex', justifyContent: 'flex-end', alignItems: 'center', position: 'relative', marginBottom: '20px' }}>
            <hr style={{ width: '100%', position: 'absolute' }} />
            <Tooltip title={<Typography style={{ fontSize: '1em' }}>Click For all articles</Typography>}>
              <Button
                style={{
                  backgroundColor: 'black',
                  color: 'white',
                  zIndex: 1,
                  textTransform: 'none',
                  position: 'relative',
                }}
                component={Link}
                to={`/`}
                onMouseOver={(e) => (e.currentTarget.style.backgroundColor = '#555')}
                onMouseOut={(e) => (e.currentTarget.style.backgroundColor = 'black')}
              >
                {toTitleCase(folderName)}
              </Button>
            </Tooltip>
          </div>
          <Grid container spacing={3}>
            {nodes.map(({ node }) => (
              <GridListTileComponent key={node.id} node={node} />
            ))}
          </Grid>
        </>
      ))}
    </>
  );
}
